export function satro(e) {
    var t = [];
    var n = [];
    var r = [];
    var i = ["a", "e", "i", "o", "u"];
    var s = e.split("");
    s.forEach(function (e) {
        t.push(e);
        i.forEach(function (n) {
            var r = new RegExp(n, "gi");
            if (e.match(r)) {
                var i = e.replace(e, "|");
                t.push(i)
            }
        })
    });
    var o = t.join("");
    var u = o.split(" ");
    u.forEach(function (e) {
        var t = (e.match(/\|/g) || []).length;
        var r = Math.round(t / 2);
        if (e.slice(-1) == "|") {
            e = e.substring(0, e.length - 1)
        }
        var i = e.split("|");
        t = 0;
        i.forEach(function (e) {
            t++;
            if (t == r) {
                n.push(e + "|")
            } else {
                n.push(e)
            }
        });
        n.push(" ")
    });
    var a = n.join("");
    var f = a.split(" ");
    f.forEach(function (e) {
        var t = e.split("|");
        if (t[1]) {
            r.push(t[1] + t[0])
        } else {
            r.push(t[0])
        }
    });
    var l = r.join(" ");
    return l
}
